export default {
  state: {
    currentPosition: null,
  },
  getters: {
    getCurrentPosition(state) {
      return state.currentPosition;
    },
  },
  mutations: {
    updateCurrentPosition(state, data) {
      state.currentPosition = data;
    },
  },
  actions: {
    setCurrentPosition(context, payload) {
      context.commit('updateCurrentPosition', payload);
    },
  },
};
