import axios from 'axios';

export default {
  state: {
    listA: [],
    listB: [],
  },
  getters: {
    getListA(state) {
      return state.listA;
    },
    getListB(state) {
      return state.listB;
    },
  },
  mutations: {
    updateListA(state, list) {
      state.listA = list;
    },
    updateListB(state, list) {
      state.listB = list;
    },
  },
  actions: {
    allList(context, payload) {
      axios.get('https://catalog.api.2gis.ru/2.0/geo/search', {
        headers: {
          Accept: 'application/json',
        },
        params: {
          key: context.rootState.key,
          q: payload.input,
          region_id: payload.region_id,
        },
      }).then((response) => {
        let items = [];
        if (response.data.meta.code === 200) {
          items = response.data.result.items.filter((item) => item.id)
            .map((item) => ({
              id: item.id,
              text: item.name,
            }));
        }
        context.commit(`updateList${payload.point.toUpperCase()}`, items);
      });
    },
  },
};
