<template>
  <div>
    <v-autocomplete
      v-model="select"
      :loading="loading"
      :items="items"
      :search-input.sync="search"
      cache-items
      class="mx-4"
      flat
      hide-no-data
      clearable
      hide-details
      :label="point === 'a' ? 'Пункт отправления' : 'Пункт назначения'"
      @input="select ? submitAddress(select) : null"
      solo-inverted
      required
      return-object
    ></v-autocomplete>
  </div>
</template>

<script>
export default {
  name: 'Input',
  props: ['point', 'map',
    'layer', 'getPosition', 'deleteMarker'],
  data: () => ({
    select: null,
    loading: false,
    search: null,
    items: [
      {
        text: 'Мое местоположение',
        id: 1,
      },
    ],
    result: [],
  }),
  mounted() {
    this.$store.watch((state, getters) => getters[`getList${this.point.toUpperCase()}`], (newList) => {
      if (newList) {
        this.result = this.result.concat(...newList);
      }
    });

    this.$store.watch((state, getters) => getters[`getPointData${this.point.toUpperCase()}`],
      (newData, oldData) => {
        if (!oldData && newData.name === 'Мое местоположение') {
          // eslint-disable-next-line prefer-destructuring
          this.select = this.items[0];
        }
      });
  },
  watch: {
    layer() {
      if (!this.layer) {
        this.search = null;
        this.select = null;
      }
    },
    search(val) {
      // eslint-disable-next-line no-unused-expressions
      val && val !== this.select && this.querySelections(val);
    },
    select() {
      if (!this.select || this.select === '') this.deleteMarker(this.point);
    },
  },
  methods: {
    querySelections(v) {
      this.loading = true;
      this.$store.dispatch('allList', {
        input: v,
        region_id: this.$store.getters.getRegionId,
        point: this.point,
      });
      setTimeout(() => {
        this.items = this.result.filter((e) => (e.text || '').toLowerCase()
          .indexOf((v || '').toLowerCase()) > -1);
        this.loading = false;
      }, 500);
    },
    submitAddress(select) {
      if (select.text === 'Мое местоположение') {
        this.getPosition(this.point);
      } else if (select.id) {
        this.$store.dispatch('setPoint', {
          id: select.id,
          point: this.point,
        });
      }
    },
  },
};
</script>

