import axios from 'axios';

export default {
  state: {
    routeData: null,
  },
  getters: {
    getRouteData(state) {
      return state.routeData;
    },
  },
  mutations: {
    updateRouteData(state, data) {
      state.routeData = data;
    },
  },
  actions: {
    setRouteData(context, payload) {
      const body = {
        locale: 'ru',
        point_a_name: payload.pointNameA,
        point_b_name: payload.pointNameB,
        points: [
          {
            type: 'pedo',
            x: payload.pointA[0],
            y: payload.pointA[1],
          },
          {
            type: 'pedo',
            x: payload.pointB[0],
            y: payload.pointB[1],
          },
        ],
        type: 'jam',
      };
      axios.post(`https://catalog.api.2gis.ru/carrouting/4.0.0/${payload.city}/`, body, {
        headers: {
          Accept: 'application/json',
        },
        params: {
          key: context.rootState.key,
        },
      }).then((response) => {
        if (response.data) {
          context.commit('updateRouteData', response.data.result[0]);
        }
      });
    },
  },
};
