import axios from 'axios';
import { transliterate as tr } from 'transliteration';

export default {
  state: {
    regionId: null,
    isGlobal: null,
    regions: null,
    cityName: null,
  },
  getters: {
    getRegionId(state) {
      return state.regionId;
    },
    isGlobal(state) {
      return state.isGlobal;
    },
    getRegions(state) {
      return state.regions;
    },
    getCityName(state) {
      return state.cityName;
    },
  },
  mutations: {
    updateRegionId(state, id) {
      state.regionId = id;
    },
    updateRegions(state, regions) {
      state.regions = regions;
    },
    updateCityName(state, cityName) {
      state.cityName = cityName;
    },
    updateIsGlobal(state, value) {
      state.isGlobal = value;
    },
  },
  actions: {
    setRegionId(context, payload) {
      axios.get('https://catalog.api.2gis.ru/2.0/geo/search', {
        headers: {
          Accept: 'application/json',
        },
        params: {
          key: context.rootState.key,
          point: payload.point,
          fields: 'items.region_id',
          radius: 2000,
        },
      }).then((response) => {
        if (response.data.meta.code === 200) {
          const items = response.data.result.items.find((i) => i.region_id);
          const id = items.region_id;
          context.commit('updateRegionId', id);
        } else {
          context.commit('updateIsGlobal', true);
        }
      });
    },
    allRegions(context) {
      axios.get('https://catalog.api.2gis.ru/2.0/region/list', {
        headers: {
          Accept: 'application/json',
        },
        params: {
          key: context.rootState.key,
        },
      }).then((response) => {
        if (response.data.meta.code === 200) {
          context.commit('updateRegions', response.data.result.items);
        }
      });
    },
    setCityName(context, payload) {
      axios.get('https://catalog.api.2gis.ru/2.0/region/get', {
        headers: {
          Accept: 'application/json',
        },
        params: {
          key: context.rootState.key,
          id: payload.id,
        },
      }).then((response) => {
        if (response.data.meta.code === 200) {
          const cityName = response.data.result.items[0].name.toLowerCase();
          context.commit('updateCityName', tr(cityName));
        }
      });
    },
  },
};
