<template>
  <v-app>
    <v-content class="d-flex align-center" style="max-height: 100vh; overflow: hidden;">
      <v-card class="bar pb-7 pt-3 px-2 text-center"
              elevation="3"
              max-width="768">
        <div class="inputRow d-flex flex-row align-center">
          <label for="point_a" class="pointLabel">А</label>
          <v-input point="a" :map="map" :layer="layerA"
                   :getPosition="getPosition"
                   :deleteMarker="deleteMarker"
                   name="point_a" class="pointInput"></v-input>
        </div>
        <div class="inputRow d-flex mb-2 flex-row align-center">
          <label for="point_a" class="pointLabel">Б</label>
          <v-input point="b" :map="map" :layer="layerB"
                   :getPosition="getPosition"
                   :deleteMarker="deleteMarker"
                   name="point_b" class="pointInput"></v-input>
        </div>
        <v-btn small absolute fab left elevation="4"
               @click="getPosition('')" color="primary">
          <v-icon color="white">mdi-map-marker</v-icon>
        </v-btn>
        <v-btn small fab absolute elevation="4"
               color="primary"
               @click="toggleCard"
               style="left: 50%; transform: translateX(-100%);">
          <v-icon color="white" id="cardBtn">mdi-arrow-up</v-icon>
        </v-btn>
        <v-btn large absolute right elevation="4"
               color="success" class="route" @click="getRouteData">В путь</v-btn>
      </v-card>
      <v-card id="map" class="mx-auto map" max-width="768"></v-card>
    </v-content>
    <v-footer app fixed max-width="768"
              class="myFooter mx-auto pb-3 pt-3 primary" elevation="25">
      <!--<v-btn small fab top absolute elevation="4"
             color="success"
             @click="toggleFooter"
             style="left: 50%; transform: translateX(-50%);">
        <v-icon color="white" id="footerBtn">mdi-arrow-up</v-icon>
      </v-btn>-->
      <v-card style="border-radius: 0;"
              class="px-2 py-2 text-center"
              width="50%">
        <span class="headline font-weight-bold primary--text">Счетчик</span>
      </v-card>
      <v-card style="border-radius: 0;"
              class="px-2 py-2 text-center"
              width="50%">
        <span class="headline font-weight-medium primary--text">{{ counter }} км.</span>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
// eslint-disable-next-line import/extensions
import Input from './components/Input';

export default {
  name: 'App',
  components: {
    vInput: Input,
  },
  data: () => ({
    map: null,
    layerA: null,
    layerB: null,
    layerRoute: null,
    layerUser: null,
    watchID: null,
    prevDistance: null,
    counter: 0,
  }),
  mounted() {
    this.setMap();

    // eslint-disable-next-line no-undef
    this.getPosition('a');

    // eslint-disable-next-line max-len
    this.$store.watch((state, getters) => getters.getCurrentPosition,
      (newPosition, oldPosition) => {
        if (!oldPosition && newPosition) {
          this.$store.dispatch('setRegionId',
            { point: `${newPosition.position[1]}, ${newPosition.position[0]}` });
          this.$store.dispatch('setEachPoint', {
            whichPoint: newPosition.point,
            point: [...newPosition.position].reverse(),
          });
          this.$store.dispatch('setEachDataPoint', {
            name: 'Мое местоположение',
            whichPoint: newPosition.point,
            point: [...newPosition.position].reverse(),
          });
        } else if (oldPosition && newPosition.point) {
          this.$store.dispatch('setEachPoint', {
            whichPoint: newPosition.point,
            point: [...newPosition.position].reverse(),
          });
          this.$store.dispatch('setEachDataPoint', {
            name: 'Мое местоположение',
            whichPoint: newPosition.point,
            point: [...newPosition.position].reverse(),
          });
        } else if (oldPosition && newPosition.point === '') {
          if (!this.layerUser) {
            this.setUserMarker(newPosition.position);
          }
          this.setViewMarker(newPosition.position, 12);
        }

        if (newPosition) {
          if (!this.layerUser) {
            this.setUserMarker(newPosition.position);
          } else {
            // eslint-disable-next-line no-undef,new-cap
            this.moveUserMarker(new DG.latLng(newPosition.position));
          }
        }
      });

    this.$store.watch((state, getters) => getters.getPointDataA, (newData) => {
      if (newData) {
        const position = [newData.point[0], newData.point[1]].reverse();
        const pointText = 'Пункт Отправления';

        this.equalAddress('b', newData);

        this.setMarker(position, pointText,
          `${newData.name} - ${pointText}`, 'a');
        this.setViewMarker(position, 15);
        if (this.layerRoute) {
          this.layerRoute.remove();
          if (this.watchID && this.counterID) {
            navigator.geolocation.clearWatch(this.watchID);
            this.watchID = null;
          }
          const pointB = this.$store.getters.getPointB;
          const positionB = [pointB[0], pointB[1]].reverse();
          const pointTextB = 'Пункт назначения';
          const pointDataB = this.$store.getters.getPointDataB;
          this.setMarker(positionB, pointTextB,
            `${pointDataB.name} - ${pointTextB}`, 'b');
        }
      }
    });
    this.$store.watch((state, getters) => getters.getPointDataB, (newData) => {
      if (newData) {
        const position = [newData.point[0], newData.point[1]].reverse();
        const pointText = 'Пункт назначения';

        this.equalAddress('a', newData);

        this.setMarker(position, pointText,
          `${newData.name} - ${pointText}`, 'b');
        this.setViewMarker(position, 15);
        if (this.layerRoute) {
          this.layerRoute.remove();
          if (this.watchID && this.counterID) {
            navigator.geolocation.clearWatch(this.watchID);
            this.watchID = null;
          }
          const pointA = this.$store.getters.getPointA;
          const positionA = [pointA[0], pointA[1]].reverse();
          const pointTextA = 'Пункт отправления';
          const pointDataA = this.$store.getters.getPointDataA;
          this.setMarker(positionA, pointTextA,
            `${pointDataA.name} - ${pointTextA}`, 'a');
        }
      }
    });

    this.$store.watch((state, getters) => getters.getRegionId, (newId) => {
      if (newId) {
        this.$store.dispatch('setCityName', { id: newId });
      }
    });

    this.$store.watch((state, getters) => getters.getRouteData, (newRoute) => {
      if (newRoute) {
        this.displayRoute(newRoute);
        this.watchPosition();
        this.toggleCard(document.querySelector('#cardBtn'));
      }
    });
  },
  methods: {
    equalAddress(point, newData) {
      const letter = point.toUpperCase();
      if (this[`layer${letter}`] && this.$store.getters[`getPointData${letter}`]) {
        if (this.$store.getters[`getPointData${letter}`].name === newData.name) {
          this.deleteMarker(point);
          this[`layer${letter}`] = null;
          this.$store.dispatch('setEachDataPoint', {
            whichPoint: point,
            name: null,
            point: null,
          });
          this.$store.dispatch('setEachPoint', {
            whichPoint: point,
            point: null,
          });
        }
      }
    },
    setMap() {
      // eslint-disable-next-line no-undef
      this.map = DG.map('map', {
        center: [43.238949, 76.889709],
        zoom: 12,
        zoomControl: false,
        fullscreenControl: false,
        minZoom: 8,
      });
    },
    getPosition(point) {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          const lat = position.coords.latitude;
          const lon = position.coords.longitude;
          this.$store.dispatch('setCurrentPosition',
            {
              position: [lat, lon],
              point,
            });
        });
      }
    },
    watchPosition() {
      if (navigator.geolocation) {
        this.watchID = navigator.geolocation.watchPosition((position) => {
          const lat = position.coords.latitude;
          const lon = position.coords.longitude;
          this.$store.dispatch('setCurrentPosition', {
            position: [lat, lon],
            point: null,
          });
          this.updateCounter(this.$store.getters.getPointA, { position: [lat, lon] });
        }, null, {
          enableHighAccuracy: true,
          timeout: 200,
        });
      }
    },
    updateCounter(pointA, currentPosition) {
      const distance = this.getDistance(pointA, currentPosition);
      if (this.prevDistance) {
        this.counter += Math.abs(distance - this.prevDistance);
        this.prevDistance = distance;
      } else {
        this.prevDistance = distance;
        this.counter = 0;
      }
    },
    getDistance(pointA, currentPosition) {
      if (pointA && currentPosition.position) {
        let curPos = currentPosition.position;
        curPos = [curPos[0], curPos[1]];
        const pA = [pointA[0], pointA[1]].reverse();
        // eslint-disable-next-line no-undef
        const latLngP = DG.latLng(curPos);
        // eslint-disable-next-line no-undef
        const latLngA = DG.latLng(pA);
        const distance = latLngA.distanceTo(latLngP);
        return +(Math.floor((distance / 1000) * 100) / 100).toFixed(2);
      }
      return 0;
    },
    setMarker(position, title, desc, point) {
      this.deleteMarker(point);
      // eslint-disable-next-line no-undef
      this[`layer${point.toUpperCase()}`] = DG.marker(position,
        { title }).addTo(this.map).bindPopup(desc);
    },
    deleteMarker(point) {
      if (this[`layer${point.toUpperCase()}`]) {
        this[`layer${point.toUpperCase()}`].remove();
      }
    },
    setViewMarker(position, zoom) {
      this.map.setView(position, zoom);
    },
    moveUserMarker(latlng) {
      this.layerUser.setLatLng(latlng);
    },
    setUserMarker(position) {
      // eslint-disable-next-line no-undef
      const userIcon = DG.divIcon({
        html: '<div style="width: 50px; height: 50px; border-radius: 50%;'
          + 'background: rgba(15, 110, 193, 0.31);'
          + 'transform: translate(-30%, -30%); position: relative">'
          + '<div style="width: 23px; height: 23px; border-radius: 13px; background: #fff;'
          + 'border: 2px solid #0f6ec1; position: absolute;'
          + 'top: 50%; left: 50%;'
          + 'transform: translate(-50%, -50%)">'
          + '<img src="./img/car.png" width="12" style="position: absolute; top: 50%; left: 50%;'
          + 'transform: translate(-50%, -50%)" alt="M"></div></div>',
        className: 'user-icon',
      });

      if (this.layerUser) this.layerUser.remove();
      // eslint-disable-next-line no-undef
      this.layerUser = DG.marker([position[0], position[1]], {
        icon: userIcon,
      }).addTo(this.map).bindPopup('Вы здесь');
    },
    getRouteData() {
      const pointA = this.$store.getters.getPointA;
      const pointB = this.$store.getters.getPointB;
      if (pointA && pointB) {
        const pointNameA = this.$store.getters.getPointDataA.name;
        const pointNameB = this.$store.getters.getPointDataB.name;

        this.$store.dispatch('setRouteData', {
          city: this.$store.getters.getCityName,
          pointNameA,
          pointNameB,
          pointA,
          pointB,
        });

        this.startDistance = this.getDistance(this.$store.getters.getCurrentPosition, pointB);
      }
    },
    createIconPoint(point) {
      const letter = point === 'a' ? 'А' : 'Б';
      // eslint-disable-next-line no-undef
      return DG.divIcon({
        html:
          '<div style="width: 20px; height: 20px; border-radius: 13px; background: #0f6ec1;'
          + 'border: 3px solid #fff; transform: translate(-3px, -3px); position: relative">'
          + '<span style="position: absolute; top: 50%; left: 50%;'
          + 'transform: translate(-45%, -45%); font-family: sans-serif;'
          + `font-weight: 700; color: #fff; font-size: 10px;">${letter}`
          + '</span></div>',
        className: 'my-point',
      });
    },
    displayRoute(routeData) {
      const colors = {
        ignore: '#0f6ec1',
        pedestrian: '#626262',
        normal: '#ffc402',
        'no-traffic': '#22aa01',
        fast: '#22aa01',
        slow: '#ed2301',
        'slow-jams': '#851705',
      };

      const weight = 5;
      // eslint-disable-next-line no-undef
      const route = DG.layerGroup();

      // eslint-disable-next-line no-undef
      const background = DG.layerGroup();
      // eslint-disable-next-line no-undef
      const backgroundDark = DG.layerGroup();
      // eslint-disable-next-line no-undef
      const fill = DG.layerGroup();

      const result = routeData;

      // eslint-disable-next-line no-undef
      const pointIconA = this.createIconPoint('a');
      // eslint-disable-next-line no-undef
      const pointIconB = this.createIconPoint('b');
      const pointA = result.waypoints[0];
      const pointB = result.waypoints[1];

      // eslint-disable-next-line no-undef
      DG.marker([pointA.projected_point.lat, pointA.projected_point.lon], {
        icon: pointIconA,
      }).addTo(fill);
      // eslint-disable-next-line no-undef
      DG.marker([pointB.projected_point.lat, pointB.projected_point.lon], {
        icon: pointIconB,
      }).addTo(fill);

      this.layerA.remove();
      this.layerB.remove();

      if (result.begin_pedestrian_path) {
        // eslint-disable-next-line no-undef
        DG.Wkt.geoJsonLayer(result.begin_pedestrian_path.geometry.selection, {
          style: () => ({
            color: '#ffffff',
            weight: 8,
          }),
        }).addTo(background);
        // eslint-disable-next-line no-undef
        DG.Wkt.geoJsonLayer(result.begin_pedestrian_path.geometry.selection, {
          style: () => ({
            color: colors.pedestrian,
            weight,
          }),
        }).addTo(fill);
      }

      if (result.end_pedestrian_path) {
        // eslint-disable-next-line no-undef
        DG.Wkt.geoJsonLayer(result.end_pedestrian_path.geometry.selection, {
          style: () => ({
            color: '#ffffff',
            weight: 8,
          }),
        }).addTo(background);
        // eslint-disable-next-line no-undef
        DG.Wkt.geoJsonLayer(result.end_pedestrian_path.geometry.selection, {
          style: () => ({
            color: colors.pedestrian,
            weight,
          }),
        }).addTo(fill);
      }

      result.maneuvers.forEach((maneuver) => {
        if (maneuver.outcoming_path) {
          maneuver.outcoming_path.geometry.forEach((geometry) => {
            // eslint-disable-next-line no-undef
            DG.Wkt.geoJsonLayer(geometry.selection, {
              style: () => ({
                color: '#000',
                weight: 11,
              }),
            }).addTo(backgroundDark);
            // eslint-disable-next-line no-undef
            DG.Wkt.geoJsonLayer(geometry.selection, {
              style: () => ({
                color: '#ffffff',
                weight: 10,
              }),
            }).addTo(background);
            // eslint-disable-next-line no-undef
            DG.Wkt.geoJsonLayer(geometry.selection, {
              style: () => ({
                color: colors[geometry.color],
                weight,
              }),
            }).addTo(fill);
          });
        }
      });

      backgroundDark.addTo(route);
      background.addTo(route);
      fill.addTo(route);
      this.layerRoute = route;
      this.layerRoute.addTo(this.map);
    },
    toggleCard(e) {
      let icon = null;
      if (e.target) {
        if (e.target.classList.contains('v-icon')) {
          icon = e.target;
        } else if (e.target.classList.contains('v-btn__content')) {
          icon = e.target.firstElementChild;
        } else if (e.target.classList.contains('v-btn')) {
          icon = e.target.firstElementChild.firstElementChild;
        }
      } else {
        icon = e;
      }
      this.toggle(icon, 'bar');
    },
    toggle(icon, className) {
      if (!icon.closest(`.${className}`).classList.contains('active')) {
        icon.classList.remove('mdi-arrow-up');
        icon.classList.add('mdi-arrow-down');
        icon.closest(`.${className}`).classList.add('active');
      } else {
        icon.classList.remove('mdi-arrow-down');
        icon.classList.add('mdi-arrow-up');
        icon.closest(`.${className}`).classList.remove('active');
      }
    },
  },
};
</script>

<style scoped>
  .bar {
    position: fixed;
    display: block;
    left: 50%;
    top: 0;
    z-index: 1;
    transform: translateX(-50%);
    width: 100%;
    padding: 10px 0;
    transition: all ease .2s;
  }

  .bar.active {
    top: -140px;
  }

  /*.myFooter {*/
  /*  bottom: -50px !important;*/
  /*  transition: all ease .2s;*/
  /*}*/

  /*.myFooter.active {*/
  /*  bottom: 0 !important;*/
  /*}*/

  .inputRow {
    padding: 0 10px;
  }

  .pointLabel {
    font-size: 20px;
    font-weight: 700;
  }

  .pointInput {
    width: 100%;
    margin: 8px 0;
  }

  .map {
    width: 100%;
    height: 100vh;
  }
</style>
