import axios from 'axios';

export default {
  state: {
    pointA: null,
    pointB: null,
    pointDataA: null,
    pointDataB: null,
  },
  getters: {
    getPointA(state) {
      return state.pointA;
    },
    getPointB(state) {
      return state.pointB;
    },
    getPointDataA(state) {
      return state.pointDataA;
    },
    getPointDataB(state) {
      return state.pointDataB;
    },
  },
  mutations: {
    updatePointA(state, point) {
      state.pointA = point;
    },
    updatePointB(state, point) {
      state.pointB = point;
    },
    updatePointDataA(state, data) {
      state.pointDataA = data;
    },
    updatePointDataB(state, data) {
      state.pointDataB = data;
    },
  },
  actions: {
    setPoint(context, payload) {
      axios.get('https://catalog.api.2gis.ru/2.0/geo/get', {
        headers: {
          Accept: 'application/json',
        },
        params: {
          key: context.rootState.key,
          id: payload.id,
          fields: 'items.geometry.selection',
        },
      })
        .then((response) => {
          if (response.data.meta.code === 200) {
            const regex = /[+-]?\d+(\.\d+)?/g;
            let point = response.data.result.items[0].geometry.selection;
            point = point.match(regex)
              .map((v) => parseFloat(v));
            if (point.length > 2) {
              point = [point[0], point[1]];
            }
            const data = response.data.result.items[0];
            context.commit(`updatePointData${payload.point.toUpperCase()}`,
              { name: data.name, point });
            context.commit(`updatePoint${payload.point.toUpperCase()}`, point);
          }
        });
    },
    setEachPoint(context, payload) {
      if (!payload.point) {
        context.commit(`updatePoint${payload.whichPoint.toUpperCase()}`, null);
      } else {
        context.commit(`updatePoint${payload.whichPoint.toUpperCase()}`, payload.point);
      }
    },
    setEachDataPoint(context, payload) {
      if (!payload.point && !payload.name) {
        context.commit(`updatePointData${payload.whichPoint.toUpperCase()}`, null);
      } else {
        context.commit(`updatePointData${payload.whichPoint.toUpperCase()}`, {
          name: payload.name,
          point: payload.point,
        });
      }
    },
  },
};
